import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
// {
//   /* The following line can be included in your src/index.js or App.js file */
// }
import { useState, useEffect } from 'react';
import { Navbar, Container, Nav, Row, Col, NavLink } from 'react-bootstrap';
// 리액트 라우터
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
// 스와이퍼
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
// import required modules
import { Navigation } from 'swiper/modules';
// 데이터
import data from './data.js'
// 소개 페이지
import Introduce from './introduce.js'
// 문의 페이지
import Inquiry from './Inquiry.js'

function App() {
  let [site] = useState(data);
  const [activeNav, setActiveNav] = useState('design');

  const NavClick = (nav) => {
      setActiveNav(nav);
  }

  return (
    <div className="App">
      {/* nav */}
      <Navbar bg='white'>
        <Container className='nav'>
          <Navbar.Brand href="/" style={{fontSize: '30px'}}>nayeong kim <span style={{color: '#ffac97', fontWeight: 'bold'}}>portfolio</span></Navbar.Brand>
          <Nav className='justify-content-end'>
            <Link to="/" className={`nav-menu ${activeNav === 'design' ? 'active' : ''}`} onClick={() => NavClick('design')}>
              디자인
            </Link>
            <Link to="/Introduce" className={`nav-menu ${activeNav === 'introduce' ? 'active' : ''}`} onClick={() => NavClick('introduce')}>
              소개
            </Link>
            <Link to="/Inquiry" className={`nav-menu ${activeNav === 'inquiry' ? 'active' : ''}`} onClick={() => NavClick('inquiry')}>
              문의
            </Link>
          </Nav>
        </Container>
      </Navbar>
      <Routes>
        <Route path = "/" element={<Main site={site}/>}></Route>
        <Route path="/Introduce" element={<Introduce/>}></Route>
        <Route path="/Inquiry" element={<Inquiry/>}></Route>
      </Routes>
    </div>
  );
}

function Main({site}) {
  let [fade, setFade] = useState('');

  useEffect(()=>{
    setTimeout(()=>{setFade('end')}, 100);
    return () => {
      setFade('')
    }
  }, [])

  return (
    <div className={`start ${fade}`}>
      <div style={{margin: '50px 0 0 0'}}>
        <p className='banner-title'>이젠컴퓨터아트서비스학원 디지털 디자인</p>
        <p className='banner-sub'>2022. 10. 04 - 2023. 01. 31</p>
      </div>
      <div>
        <Swiper navigation={true} modules={[Navigation]} className="mySwiper">
          {site.map((item, i) => {
            return (
              <SwiperSlide key={i}><Item item={item} i={i}></Item></SwiperSlide>
            )
          })}
        </Swiper>
      </div>
      
      <div className='line'></div>

      <div style={{margin: '50px 0'}}>
        <p className='banner-title'>전주맛배달</p>
        <p className='banner-sub'>2023. 04. 10 - 2023. 07. 10</p>
      </div>

      <div>
        <div style={{marginBottom: '20px'}}>
          <img src={process.env.PUBLIC_URL + '/widthbanner1.jpg'} style={{marginRight: '20px'}}></img>
          <img src={process.env.PUBLIC_URL + '/widthbanner2.jpg'}></img>
        </div>
        <div style={{marginBottom: '20px'}}>
          <img src={process.env.PUBLIC_URL + '/widthbanner3.jpg'} style={{marginRight: '20px'}}></img>
          <img src={process.env.PUBLIC_URL + '/widthbanner4.jpg'}></img>
        </div>
      </div>

      <div className='sub-title' style={{marginBottom: '20px'}}>
        <p>어플 내 가맹점 로고 제작</p>
      </div>

      <div className='banner-wrap' style={{display: 'flex'}}>
        <div style={{marginRight: '20px'}}>
          <img src={process.env.PUBLIC_URL + '/heightbanner2.jpg'}></img>
          <div className='yellowBox'></div>
        </div>
        <div>
          <img src={process.env.PUBLIC_URL + '/heightbanner1.jpg'}></img>
        </div>  
      </div>

      <div className='sub-title' style={{marginBottom: '50px'}}>
        <p>외부행사 배너 디자인 제작</p>
      </div>
    </div>
  )
}

function Item({item}) {
  // let navigate = useNavigate()
  const ClickSite = () => {
    if (window.confirm('사이트로 이동 하시겠습니까?')) {
      window.open(item.site);
    }
  }

  return (
    <div style={{display: 'flex'}}>
      <div>
        <img src={item.src}/>
      </div>
      <div className='siteSub'>
        <div style={{marginBottom: '20px'}}><img src={item.image}/></div>
        <div style={{marginBottom: '20px'}}>{item.content1}</div>
        <div style={{fontWeight: 'bold', marginBottom: '20px'}}>{item.content2}</div>
        <div className='siteBtn' onClick={ClickSite}>바로가기</div>
      </div>
    </div>
  );
}

export default App;
