import { useState, useEffect, useRef, useCallback } from 'react';
import { InquiryInputWrap, InquiryInput, SendBtnWrap, SendBtn, InquiryInput2, InquiryInput3, InquirySelect } from './css.js';
import './App.css';
// email
import emailjs from '@emailjs/browser';

function Inquiry() {
  let [fade, setFade] = useState('');
  // name
  let [userName, setUserName] = useState('');
  // eamil
  let [userEmail, setUserEmail] = useState('');
  // tel
  let [userTel, setUserTel] = useState('');
  // error message
  let [errorMsg1, setErrorMsg1] = useState('');
  let [errorMsg2, setErrorMsg2] = useState('');
  let [errorMsg3, setErrorMsg3] = useState('');

  // form
  const form = useRef();

  const SendEmail = (e) => {
    e.preventDefault();

    if (!userName || !userEmail || !userTel) {
      alert('입력하지 않으셨습니다.');
      return;
    }

    emailjs.sendForm('nyang111', 'template_9azdbc5', form.current, 'gZ8O0ldV9O6B-bf2N').then((result) => {
      alert('이메일이 성공적으로 전송 되었습니다.');
      form.current.reset();
    }).catch((error) => {
      console.log(error.text);
      alert('이메일 전송이 실패 되었습니다.')
    })
  }

  // 정규식
  const onChangeName = useCallback((e) => {
    // 한글이 쓰이는지
    const korea = /^[ㄱ-ㅎ가-힣ㅏ-ㅣ]+$/;
    if (e.target.value === '' || korea.test(e.target.value)) {
      setUserName(e.target.value);
      setErrorMsg1('');
    } else {
      setErrorMsg1('한글을 입력해 주세요.')
    }
  })

  const onChangeEmail = useCallback((e) => {
    // 영어가 쓰이는지
    const mailFormat = /^[a-z]+$/;

    if (e.target.value === '' || mailFormat.test(e.target.value)) {
      setUserEmail(e.target.value);
      setErrorMsg2('');
    } else {
      setErrorMsg2('영문을 입력해 주세요.');
    }
  })

  const onChangeTel = useCallback((e) => {
    // 번호가 쓰이는지
    const number = /^[0-9]+$/;
    if (e.target.value === '' || number.test(e.target.value)) {
      setUserTel(e.target.value);
      setErrorMsg3('');
    } else {
      setErrorMsg3('번호를 입력해 주세요.')
    }
  })

  useEffect(()=>{
    setTimeout(()=>{setFade('end')}, 100);
    return () => {
      setFade('');
    }
  }, []);

  return (
    <div className={`start ${fade}`}>
      <div style={{marginBottom: '50px'}}>
        <img src={process.env.PUBLIC_URL + '/email.png'} style={{margin: '50px 0'}}></img>
        <p style={{fontSize: '30px', fontWeight: 'bold'}}>Send Me an Email!</p>
        <form ref={form} onSubmit={SendEmail}>
          <InquiryInputWrap>
            <InquiryInput placeholder='이름' type='text' name='name' onChange={onChangeName} value={userName}/>
            <p style={{color: 'red', margin: '0', textAlign: 'justify'}}>{errorMsg1}</p>

            <div>
              <div style={{display: 'flex'}}>
                <InquiryInput3 placeholder='이메일' type='text' name='mail' onChange={onChangeEmail} value={userEmail}/>
                <InquirySelect>
                  <option value="option1">@naver.com</option>
                  <option value="option2">@google.com</option>
                  <option value="option3">@daum.com</option>
                  <option value="option4">@nate.com</option>
                </InquirySelect>
              </div>
              <p style={{color: 'red', margin: '0', textAlign: 'justify'}}>{errorMsg2}</p>
            </div>

            <InquiryInput placeholder='전화번호' type='tel' name='tel' onChange={onChangeTel} value={userTel}/>
            <p style={{color: 'red', margin: '0', textAlign: 'justify'}}>{errorMsg3}</p>

            <InquiryInput2 placeholder='메시지를 입력하세요.' style={{height: '194px'}} name='message'/>
          </InquiryInputWrap>
          <SendBtnWrap type='submit'>
            <SendBtn>SEND</SendBtn>
          </SendBtnWrap>
        </form>
      </div>
    </div>
  )
}

export default Inquiry;