import {
    CircleWrap, TitleWrap, BoldSpan, TitleSize,
    SubSize, SubMargin, BasicWrap, ProfilePhotoWrap,
    ProfilePhoto, ProfilePhotoSubWrap,
    ProfileEduWrap, ProfileEduDate,
    TabBtnWrap, TabBtn
} from './css.js';
import { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import './App.css';

// 탭콘텐츠
let TabUI = {
  info : <Information />,
  license : <License />
}

// export default(부모컴포넌트)
function Introduce() {
  let [fade, setFade] = useState('');

  useEffect(()=>{
    setTimeout(()=>{setFade('end')}, 100);
    return () => {
      setFade('')
    }
  }, [])

  return (
    <Content fade={fade}/>
  )
}

function Content({fade}) {
  return (
    <div className={`start ${fade}`}>
      <div style={{marginBottom: '100px'}}>
        <CircleWrap>
            <div>
              <img src={process.env.PUBLIC_URL + '/circle1.png'} style={{width: '200px', height: '200px'}}></img>
            </div>
            <div>
              <img src={process.env.PUBLIC_URL + '/circle2.png'} style={{width: '100px', height: '100px'}}></img>
            </div>
            <TitleWrap>
              <TitleSize>디자인은 <BoldSpan>심플함</BoldSpan>과 <BoldSpan>담백함</BoldSpan>에서</TitleSize>
              <TitleSize>빛을 발한다고 생각합니다.</TitleSize>
            </TitleWrap>
        </CircleWrap>
        <SubSize>
          <SubMargin>안녕하세요, 웹디자이너를 목표로 하고 있는 김나영 입니다.</SubMargin>
          <SubMargin>웹디자이너라는 꿈이 생겨 학원을 다니며 디자인의 기초와 코딩의 기초에 대해</SubMargin>
          <SubMargin>배우고, 전주맛배달에서 디자인 업무를 주로 하며 인턴으로 근무 하였습니다.</SubMargin>
        </SubSize>
      </div>

      <TabContent></TabContent>
    </div>
  )
}

function TabContent() {
  let [present, setPresent] = useState('info');
  let [activeTab, setActiveTab] = useState('info');

  const ClickBtn = (tab) => {
    setActiveTab(tab);
  }

  return (
    <>
      <TabBtnWrap>
        <TabBtn 
          active={activeTab === 'info'}
          onClick={() => {
            setPresent('info');
            ClickBtn('info')
          }}
        >학력·교육 / 경력</TabBtn>
        <TabBtn 
          active={activeTab === 'license'}
          onClick={() => {
            setPresent('license');
            ClickBtn('license')
          }}
        >자격증</TabBtn>
      </TabBtnWrap>
      <BasicWrap>
        {TabUI[present]}
      </BasicWrap>
    </>
  )
}

